/* Typography */
html {
  font-family: sans-serif;
  font-size: 16px;
  color: #111;
  /*@media screen and (min-width: $breakpoint--typography) {
    font-size: 18px;
  }*/
}

body {
  font-size: 100%;
  font-weight: 300;
  line-height: 1.6;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
}

h1 {
  margin-bottom: 1.625rem;
  font-size: 2.5rem;
  line-height: 3.25rem;
}
@media screen and (min-width: 640px) {
  h1 {
    margin-bottom: 1.9375rem;
    line-height: 3.875rem;
  }
}

h2 {
  margin-bottom: .8125rem;
  font-size: 1.6875rem;
  line-height: 2.4375rem;
}
@media screen and (min-width: 640px) {
  h2 {
    margin-bottom: .96875rem;
    line-height: 2.90625rem;
  }
}

h3 {
  margin-bottom: .8125rem;
  font-size: 1.375rem;
  line-height: 1.625rem;
}
@media screen and (min-width: 640px) {
  h3 {
    margin-bottom: .96875rem;
    line-height: 1.9375rem;
  }
}

h4 {
  margin-bottom: .8125rem;
  font-size: 1.2rem;
  line-height: 1.625rem;
}
@media screen and (min-width: 640px) {
  h4 {
    margin-bottom: .96875rem;
    line-height: 1.9375rem;
  }
}

h5 {
  margin-bottom: .8125rem;
  font-size: 1rem;
  line-height: 1.625rem;
}
@media screen and (min-width: 640px) {
  h5 {
    margin-bottom: .96875rem;
    line-height: 1.9375rem;
  }
}

h6 {
  margin-bottom: .8125rem;
  font-size: .875rem;
  font-weight: normal;
  line-height: 1.625rem;
}
@media screen and (min-width: 640px) {
  h6 {
    margin-bottom: .96875rem;
    line-height: 1.9375rem;
  }
}

h1 + h2 {
  margin-top: 1.625rem;
}
@media screen and (min-width: 640px) {
  h1 + h2 {
    margin-top: 1.9375rem;
  }
}

h2 + h3,
h3 + h4,
h4 + h5 {
  margin-top: .8125rem;
}
@media screen and (min-width: 640px) {
  h2 + h3,
  h3 + h4,
  h4 + h5 {
    margin-top: .96875rem;
  }
}

h5 + h6 {
  margin-top: -.8125rem;
}
@media screen and (min-width: 640px) {
  h5 + h6 {
    margin-top: -.96875rem;
  }
}

hgroup {
  display: block;
}

abbr {
  letter-spacing: .1em;
}
abbr[title] {
  text-decoration: underline dotted;
  /*border-bottom: 1px dotted;*/
  border-bottom: 0;
}

b,
strong {
  font-weight: bold;
}

cite {
  display: block;
  font-style: normal;
}

dfn {
  font-style: italic;
}

figure blockquote {
  padding-top: 1.625rem;
  padding-right: 0;
  padding-bottom: 0;
  padding-left: 1.625rem;
}
@media screen and (min-width: 640px) {
  figure blockquote {
    padding-top: 3.875rem;
    padding-right: 1.9375rem;
    padding-bottom: 0;
    padding-left: 1.9375rem;
    text-align: center;
  }
}
figure blockquote p {
  font-size: 1.6875rem;
  line-height: 2.4375rem;
}
@media screen and (min-width: 640px) {
  figure blockquote p {
    line-height: 2.90625rem;
  }
}

em,
i {
  position: relative;
  line-height: 0;
  vertical-align: baseline;
}

mark {
  color: #000;
  background: #ff0;
}

small {
  font-size: 80%; 
