/* Code */
pre {
  overflow: auto;
  font-family: monospace, monospace;
  font-size: 1em;
  word-wrap: break-word;
}
pre > code {
  display: block;
  padding: .5rem 1rem 1.5rem;
  white-space: pre;
}

code {
  overflow: auto; 
  padding: .2rem .5rem;
  margin: 0 .2rem;
  font-family: monospace, monospace;
  font-size: 1em;
  white-space: nowrap;
  background: #f4f5f6;
  border-radius: 4px;
}

/* Dividers */
hr {
  display: block;
  overflow: visible;
  box-sizing: content-box;
  margin-top: 3.25rem;
  margin-right: auto;
  margin-bottom: 3.5rem;
  margin-left: auto;
  border-width: 0;
  border-top: 1px solid #d9d9d9;
}
@media screen and (min-width: 640px) {
  hr {
    margin-top: 3.875rem;
    margin-bottom: 3.875rem;
  }
}

/* Forms */
::-webkit-input-placeholder {
  color: inherit;
  opacity: .54;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

input,
optgroup,
select,
textarea {
  margin: 0; 
  font: inherit;
}

fieldset {
  padding: 0;
  margin-top: 0;
  margin-right: 0;
  margin-bottom: 1.125rem;
  margin-left: 0;
  border: 0;
}

input {
  overflow: visible;
}

legend {
  display: table;
  box-sizing: border-box;
  padding: 0;
  max-width: 100%;
  color: inherit;
  white-space: normal;
}

optgroup {
  font-weight: bold;
}

select {
  text-transform: none;
}

textarea {
  overflow: auto;
  height: auto;
  min-height: 3.125rem;
  resize: vertical;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -.125rem;
}
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

select,
[type="date"],
[type="datetime"],
[type="datetime-local"],
[type="color"],
[type="email"],
[type="month"],
[type="number"],
[type="password"],
[type="range"],
[type="search"],
[type="tel"],
[type="text"],
[type="time"],
[type="url"],
[type="week"],
textarea {
  padding-top: .5625rem;
  padding-right: .5625rem;
  padding-bottom: .5625rem;
  padding-left: .5625rem;
  margin-bottom: 1.125rem;
  width: 100%;
  min-height: 2.8125rem;
  font-family: inherit;
  border: 1px solid #d9d9d9;
  border-radius: .25rem;
}
select:focus,
[type="date"]:focus,
[type="datetime"]:focus,
[type="datetime-local"]:focus,
[type="color"]:focus,
[type="email"]:focus,
[type="month"]:focus,
[type="number"]:focus,
[type="password"]:focus,
[type="range"]:focus,
[type="search"]:focus,
[type="tel"]:focus,
[type="text"]:focus,
[type="time"]:focus,
[type="url"]:focus,
[type="week"]:focus,
textarea:focus {
  outline-color: #00b4ff;
}

[type="checkbox"] {
  position: relative;
  font: normal normal normal 14px/1 FontAwesome;
  color: #00b4ff;
}
[type="checkbox"]:before {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  content: "";
  background: #fff;
  border: 1px solid #d9d9d9;
  border-radius: .25rem;
  transform: scale(1.05);
}
[type="checkbox"]:after {
  position: absolute;
  top: -1px;
  left: 0;
  z-index: 4;
  content: "\2714";
  transition: transform .125s cubic-bezier(.22, .99, 1, 1.47); 
  transform: scale(0);
}
[type="checkbox"]:checked:after {
  transform: scale(1);
}

[type="radio"] {
  position: relative;
  font: normal normal normal 14px/1 FontAwesome;
  color: #00b4ff;
}
[type="radio"]:before,
[type="radio"]:after {
  position: absolute;
  content: "";
  border-radius: 50%;
}
[type="radio"]:before {
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
  background: #fff;
  border: 1px solid #d9d9d9;
}
[type="radio"]:after {
  top: .1875rem;
  left: .1875rem;
  z-index: 3;
  width: 60%;
  height: 60%;
  background: #00b4ff;
  transition: transform .125s cubic-bezier(.22, .99, 1, 1.47); 
  transform: scale(0);
}
[type="radio"]:checked:after {
  transform: scale(1);
}

/* Links */
a {
  color: #00b4ff;
  text-decoration: none;
  background-color: transparent;
  transition: color .25s cubic-bezier(.4, 0, 1, 1); 

  -webkit-text-decoration-skip: objects;
}
a:focus,
a:hover {
  color: #0085bd;
  outline-width: 0;
}
a:hover,
a:active {
  outline: 0;
}
a:active {
  color: #0077a8;
}
a:visited {
  color: #006894;
}
a:visited:hover {
  color: #003a52;
}

/**
 * Lists
 */
ol {
  list-style: decimal inside;
}

ul {
  list-style: disc inside;
}

ol,
ul {
  padding-left: 0;
  margin-top: 0;
}
ol ul,
