  margin-bottom: 1rem;
}

/**
 * Tables
 */
.table-responsive-container {
  overflow-x: auto; 
  max-width: 100%;
}
.table-responsive-container table {
  width: 100%;
}
.table-responsive-container th,
.table-responsive-container td {
  white-space: nowrap;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
}

th,
