/* Code */

pre {
  overflow: auto;
  font-family: monospace, monospace;
  font-size: 1em;
  word-wrap: break-word;

  > code {
    display: block;
    padding: .5rem 1rem 1.5rem;
    white-space: pre;
  }
}

code {
  padding: .2rem .5rem;
  margin: 0 .2rem;
  font-family: monospace, monospace;
  font-size: 1em;
  white-space: nowrap;
  background: $color--tertiary;
  border-radius: 4px;
  overflow: auto;
}
