/**
 * Lists
 */

ol {
  list-style: decimal inside;
}

ul {
  list-style: disc inside;
}

ol,
ul {
  padding-left: 0;
  margin-top: 0;

  ul,
  ol {
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    margin-left: 1.75rem;
    font-size: 90%;
  }
}

li {
  margin-bottom: 1rem;
}
