// Grid system

.ether-container {
  padding-right: .9375rem;
  padding-left: .9375rem;
  margin-right: auto;
  margin-left: auto;
  max-width: 95%;

  @media (min-width: $screen--xm) {
    max-width: 90%;
  }

  @media (min-width: $screen--sm) {
    max-width: 85%;
  }

  @media (min-width: $screen--md) {
    max-width: remy(720px);
  }

  @media (min-width: $screen--lg) {
    max-width: remy(940px);
  }

  @media (min-width: $screen--xl) {
    max-width: remy(1140px);
  }

  @media (min-width: $screen--xxl) {
    max-width: remy(1368px);
  }

  &:after {
    display: table;
    clear: both;
    content: '';
  }

  &-full {
    padding-right: .9375rem;
    padding-left: .9375rem;
    margin-right: auto;
    margin-left: auto;

    &:after {
      display: table;
      clear: both;
      content: '';
    }
  }
}

.ether-row {
  margin-right: -.9375rem;
  margin-left: -.9375rem;

  &::after {
    display: table;
    clear: both;
    content: '';
  }
}

